.banner{
    color: white;
    object-fit: contain;
    height: 448px;
}

.banner_contents{
    margin-left: 38px;
    padding-top: 190px;
    height: 190px;
}

.banner_title{
    font-size: 3rem;
    font-weight: 700;
    padding-bottom: 1.3rem;
}

.banner_description{
    width: 40rem;
    line-height: 1.5;
    padding-top: 2rem;
    font-size: 1rem;
    font-weight: 500;
    height: 80px;
}

.banner_button{
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
    padding-bottom: 0.5rem;
}

.banner_button:hover{
    color: #000;
    background-color: #e6e6e6;
    transition: all 0.2s;
}

.banner--fadeBottom{
    height: 264px;
    background-image: linear-gradient(180deg, transparent, rgba(37, 37, 37, 0.65), #111);
}
  