.nav{
    position: fixed;
    top: 0;
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 10px;
    height: 50px;
    background-image: linear-gradient(
        180deg,
        #111,
        rgba(31, 31, 31, 0.61),
        transparent
    );
    transition-timing-function: ease-in;
    transition: all 0.5s;
}

.nav_black{
    background-color: #111;
}

.nav_logo{
    position: fixed;
    height: 30px;
    left: 20px;
    width: 80px;
    object-fit: contain;
    cursor: pointer;
}

.nav_avatar{
    position: fixed;
    right: 20px;
    width: 30px;
    object-fit: contain;
    cursor: pointer;
}